import React, { useState, useEffect } from 'react';
import Articles from "./containers/Articles";

function App() {
  const [data, setData] = useState();

  useEffect(() => {
    fetch("/api")
      .then((res) => res.json())
      .then((data) => setData(data.message));
  }, []);

  return (
    <div>
      <Articles />
      <br />
      <div className="fetched-data">
        <p>{!data ? "Loading..." : data}</p>
      </div>
    </div>
  )
}

export default App;



